import styles from "./LogDetail.module.css";
import { useParams } from "react-router-dom";
import { useSelector } from "react-redux";
import { nametoProfile, timestampTo24, timestampToDate } from "./LogList";
import yihein from "../Assets/profile/yihein.jpg";
import yikang from "../Assets/profile/yikang.jpg";
import yixuen from "../Assets/profile/yixuen.jpg";
import Chip from "../UI/Chip";
import Number from "../UI/Number";

const LogDetail = () => {
  const params = useParams();
  const route = useSelector((state) => state.route.route);
  console.log(route);
  const images = { yihein, yikang, yixuen };
  const logItem = route.find((ele) => ele.id === params.logID);
  if (!logItem) return <div></div>;
  return (
    <div className={styles["log__detail"]}>
      <figure className={styles["log__figure"]}>
        <img src={images[nametoProfile(logItem.name)]}></img>
      </figure>
      <div className={styles["log__details"]}>
        <div className={styles["log__name"]}>
          <h3>{logItem.name}</h3>
        </div>
        <p>
          This is the more detailed description, so it should contain more things, however, I do not
          know what to say so I am just wasting words here just to fill up the space.
        </p>
        <div className={styles["log__chips"]}>
          <Chip size="small">{logItem.route}</Chip>
          <Chip size="small">{timestampTo24(logItem.time)}hrs</Chip>
          <Chip size="small">{timestampToDate(logItem.time)}</Chip>
        </div>
        <div className={styles["log__time"]}>
          <Number value={logItem.duration} unit="min" />
        </div>
      </div>
    </div>
  );
};

export default LogDetail;
