import Form from "./Form/Form";
import LogList from "./Log/LogList";
import Analytics from "./Analytics/Analytics";
import styles from "./Main.module.css";
import { useDispatch, useSelector } from "react-redux";
import { getRouteList } from "./store/HorbContextProvider";
import { useEffect } from "react";
import { Redirect, Route, Switch } from "react-router-dom";

const Main = () => {
  const status = useSelector((state) => state.ui.status);
  const dispatch = useDispatch();

  useEffect(() => {
    if (status === "success" || status === "") dispatch(getRouteList());
  }, [status]);

  return (
    <div className={styles.main}>
      <Switch>
        <Route path="/horb" exact>
          <Redirect to="/horb/analytics"></Redirect>
        </Route>
        <Route path="/horb/form">
          <Form />
        </Route>
        <Route path="/horb/logs">
          <LogList />
        </Route>
        <Route path="/horb/analytics">
          <Analytics />
        </Route>
      </Switch>
    </div>
  );
};

export default Main;
