import { useSelector } from "react-redux";
import Button from "../UI/Button";
import Modal from "../UI/Modal";
import styles from "./FormModal.module.css";

const FormModal = (props) => {
  const ui = useSelector((state) => state.ui);

  const icon = {
    success: (
      <svg viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg">
        <path d="M10,17L5,12L6.41,10.58L10,14.17L17.59,6.58L19,8M12,2A10,10 0 0,0 2,12A10,10 0 0,0 12,22A10,10 0 0,0 22,12A10,10 0 0,0 12,2Z"></path>
      </svg>
    ),
    pending: (
      <svg xmlns="http://www.w3.org/2000/svg">
        <path d="M47.0001 0.333191C21.2401 0.333191 0.333397 21.2399 0.333397 46.9999C0.333397 72.7599 21.2401 93.6665 47.0001 93.6665C72.7601 93.6665 93.6667 72.7599 93.6667 46.9999C93.6667 21.2399 72.7601 0.333191 47.0001 0.333191ZM51.6667 70.3332H42.3334V60.9999H51.6667V70.3332ZM51.6667 51.6665H42.3334V23.6665H51.6667V51.6665Z" />
      </svg>
    ),
    failure: (
      <svg xmlns="http://www.w3.org/2000/svg">
        <path d="M47.0001 0.333191C21.2401 0.333191 0.333397 21.2399 0.333397 46.9999C0.333397 72.7599 21.2401 93.6665 47.0001 93.6665C72.7601 93.6665 93.6667 72.7599 93.6667 46.9999C93.6667 21.2399 72.7601 0.333191 47.0001 0.333191ZM51.6667 70.3332H42.3334V60.9999H51.6667V70.3332ZM51.6667 51.6665H42.3334V23.6665H51.6667V51.6665Z" />
      </svg>
    ),
  };
  return (
    <Modal className={styles["modal"]}>
      <div className={`${styles["modal__icon"]} ${styles[ui.status]}`}>{icon[ui.status]}</div>
      <div className={styles["modal__title"]}>{ui.title}</div>
      <div className={styles["modal__description"]}>{ui.description}</div>
      <Button onClickHandler={props.onCloseClick} size="normal" color="gray">
        Close
      </Button>
    </Modal>
  );
};

export default FormModal;
