import styles from "./Input.module.css";

const Input = (props) => {
  function inputChangeHandler(event) {
    if (event.target.value <= 0 || event.target.value >= 99) return props.onInputChange("");
    props.onInputChange(event.target.value);
  }

  const inputTouchedHandler = () => {
    props.onTouchHandler(true);
  };
  return (
    <>
      <input
        onChange={inputChangeHandler}
        onBlur={inputTouchedHandler}
        className={`${styles.input} ${styles[props.size]} ${styles[props.validation]}`}
        {...props}
      ></input>
      {props.validation === "invalid" ? (
        <label className={styles["invalid__label"]}>Please enter a valid {props.id}</label>
      ) : (
        ""
      )}
    </>
  );
};

export default Input;
