import { useDispatch } from "react-redux";
import styles from "./Nav.module.css";
import NavItem from "./NavItem";
import { NavLink } from "react-router-dom";

const Nav = () => {
  const dispatch = useDispatch();
  return (
    <ul className={styles.nav}>
      <NavLink to="/horb/logs/list" activeClassName={styles.active}>
        <NavItem variation="normal" name="logs" />
      </NavLink>
      <NavLink to="/horb/form" activeClassName={styles.active}>
        <NavItem variation="pop" name="form" />
      </NavLink>
      <NavLink to="/horb/analytics" activeClassName={styles.active}>
        <NavItem variation="normal" name="analytics" />
      </NavLink>
    </ul>
  );
};

export default Nav;
