import Header from "./Header/Header";
import "./App.css";
import Main from "./Main";
import Nav from "./Nav/Nav";
import { Provider } from "react-redux";
import store from "./store/HorbContextProvider";

const App = () => {
  return (
    <Provider store={store}>
      <Header />
      <Main />
      <Nav />
    </Provider>
  );
};

export default App;
