import { configureStore, createSlice } from "@reduxjs/toolkit";
import { useDispatch } from "react-redux";

const routeInitialState = { route: [] };

const routeSlice = createSlice({
  name: "route",
  initialState: routeInitialState,
  reducers: {
    setRouteList(state, action) {
      state.route = action.payload;
    },
  },
});

export const routeAction = routeSlice.actions;

const uiInitialState = {
  status: "",
  title: "",
  description: "",
};

const uiSlice = createSlice({
  name: "ui",
  initialState: uiInitialState,
  reducers: {
    showNotification(state, action) {
      state.status = action.payload.status;
      state.title = action.payload.title;
      state.description = action.payload.description;
    },
  },
});

export const uiActions = uiSlice.actions;

const store = configureStore({
  reducer: {
    route: routeSlice.reducer,
    ui: uiSlice.reducer,
  },
});

export const getRouteList = () => {
  return async (dispatch) => {
    try {
      const response = await fetch(
        "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json"
      );
      const data = await response.json();
      dispatch(routeAction.setRouteList(Object.values(data)));
    } catch (error) {
      console.error(`This error occured! ${error}`);
    }
  };
};

export const sendLogEntry = (inputData) => {
  return async (dispatch) => {
    async function sendRouteData(inputData) {
      const response = await fetch(
        "https://horb-32823-default-rtdb.asia-southeast1.firebasedatabase.app/route.json",
        {
          method: "POST",
          body: JSON.stringify(inputData),
          headers: {
            "Content-Type": "application/json",
          },
        }
      );
    }

    try {
      dispatch(
        uiActions.showNotification({
          status: "pending",
          title: "Pending...",
          description: "Uploading logs in progress, please be patient!",
        })
      );

      await sendRouteData(inputData);

      dispatch(
        uiActions.showNotification({
          status: "success",
          title: "Success!",
          description:
            "Your log has been submitted successfully! Check the logs tab to see your entry.",
        })
      );
    } catch (error) {
      dispatch(
        uiActions.showNotification({
          status: "failure",
          title: "Failure!",
          description:
            "Your log failed to submit ðŸ˜­! Please check your internet connection or try again later.",
        })
      );
    }
  };
};

export default store;

