import { useSelector } from "react-redux";
import Chip from "../../UI/Chip";
import Number from "../../UI/Number";
import styles from "./AverageItem.module.css";

function AverageItem(props) {
  const route = useSelector((state) => state.route.route);
  function generateAverage() {
    const routeArray = route.filter((ele) => {
      return ele.route.toLowerCase() === props.name;
    });
    return routeArray
      .reduce((acc, ele, i, arr) => {
        let calcualtedMin = 0;
        calcualtedMin = ele.duration / arr.length;
        return acc + calcualtedMin;
      }, 0)
      .toFixed(2);
  }

  return (
    <div className={styles["average__item"]}>
      <div className={styles["average__info"]}>
        <Chip size="normal" position="absolute">
          {props.name}
        </Chip>
      </div>
      <Number value={generateAverage()} unit="min" />
    </div>
  );
}

export default AverageItem;
