import Input from "../UI/Input";
import Selector from "../UI/Selector";
import styles from "./Form.module.css";
import { useState } from "react";

import Button from "../UI/Button";
import Section from "../UI/Section";
import SectionHdr from "../UI/SectionHdr";
import FormModal from "./FormModal";
import { createPortal } from "react-dom";
import { useDispatch } from "react-redux";
import { sendLogEntry } from "../store/HorbContextProvider";

const Form = (props) => {
  const dispatch = useDispatch();

  const [namevalue, setNamevalue] = useState("");
  const [routevalue, setRoutevalue] = useState("");
  const [minvalue, setMinvalue] = useState(null);
  const [dateEntry, setDateEntry] = useState(Date.now());
  const [toggleForm, setToggleForm] = useState(false);
  const [tempDate, setTempDate] = useState(new Date().toISOString().slice(0, 16));

  const [touchedName, setTouchedName] = useState(false);
  const [touchedRoute, setTouchedRoute] = useState(false);
  const [touchedTime, setTouchedTime] = useState(false);
  const [touchedDate, setTouchedDate] = useState(false);

  async function onFormSubmitHandler(event) {
    setToggleForm(true);
    dispatch(
      sendLogEntry({
        name: namevalue,
        route: routevalue,
        duration: minvalue,
        time: dateEntry ? dateEntry : Date.now(),
        id: `${minvalue}${namevalue.split(" ").reduce((acc, ele) => (acc += ele[0]), "")}${
          routevalue[0]
        }${dateEntry.toString().slice(0, 8)}`,
      })
    );
  }

  function onDateEntryHandler(input) {
    setDateEntry(new Date(input).getTime());
    setTempDate(input);
  }

  function onCloseClick() {
    setToggleForm((prevState) => !prevState);
  }

  return (
    <div className={styles["form__wrapper"]}>
      {toggleForm &&
        createPortal(
          <FormModal onCloseClick={onCloseClick} />,
          document.querySelector("#modal-content")
        )}
      <div className={styles.form}>
        <Section>
          <SectionHdr>New Entry</SectionHdr>
        </Section>
        <Selector
          className={!namevalue && touchedName ? "invalid" : ""}
          onTouchHandler={setTouchedName}
          onSelectorHandler={setNamevalue}
          name="name"
          id="name-input"
          label="Name"
          options={["Yi Hein", "Yi Xuen", "Yi Kang"]}
        ></Selector>
        <Selector
          className={!routevalue && touchedRoute ? "invalid" : ""}
          onTouchHandler={setTouchedRoute}
          onSelectorHandler={setRoutevalue}
          name="Route"
          id="route-input"
          label="Route"
          options={["HillView", "Beauty World"]}
        ></Selector>
        <div className={styles.time}>
          <label htmlFor="min">
            <h4>Time</h4>
          </label>
          <Input
            onInputChange={setMinvalue}
            validation={!minvalue && touchedTime ? "invalid" : ""}
            onTouchHandler={setTouchedTime}
            pattern="\d*"
            type="number"
            id="time"
            placeholder="min"
            size="small"
          />
        </div>
        <div className={styles.date}>
          <label htmlFor="date">
            <h4>Date</h4>
          </label>
          <Input
            onInputChange={onDateEntryHandler}
            onTouchHandler={setTouchedDate}
            type="datetime-local"
            id="date"
            size="normal"
          />
        </div>
        <Button size="normal" onClickHandler={onFormSubmitHandler} color="blue">
          Submit
        </Button>
      </div>
    </div>
  );
};

export default Form;
