import styles from "./Selector.module.css";

const Selector = (props) => {
  function onSelectorChange(event) {
    props.onSelectorHandler(event.target.value);
  }

  const onSelectorTouch = function () {
    if (!props.onTouchHandler) return;
    props.onTouchHandler(true);
  };

  const options = props.options.map((ele) => {
    return (
      <option className={styles.option} value={ele}>
        {ele}
      </option>
    );
  });
  return (
    <div className={`${styles.selector} ${styles[props.justify]}`}>
      <label className={styles.label} htmlFor={props.id}>
        <h4>{props.label}</h4>
      </label>
      <select
        onChange={onSelectorChange}
        onBlur={onSelectorTouch}
        value={props.value}
        className={`${styles.select} ${styles[props.className]}`}
        name={props.name}
        id={props.id}
      >
        <option className={styles.option} value="">
          Choose an option
        </option>
        {options}
      </select>
      {props.className === "invalid" ? (
        <label className={styles["invalid__label"]}>Please choose a valid {props.name}</label>
      ) : (
        ""
      )}
    </div>
  );
};

export default Selector;
