import { useSelector } from "react-redux";
import { dataSorter } from "../Analytics/Graph/GraphHelper";
import Section from "../UI/Section";
import SectionHdr from "../UI/SectionHdr";
import LogDetail from "./LogDetail";
import LogItem from "./LogItem";
import styles from "./LogList.module.css";
import { Route, Link, Switch } from "react-router-dom";

export const timestampTo24 = (timestamp) => {
  const processedDate = new Date(timestamp);
  return `${processedDate.getHours().toString().padStart(2, "0")}:${processedDate
    .getMinutes()
    .toString()
    .padStart(2, "0")}`;
};

export const timestampToDate = (timestamp) => {
  const processedDate = new Date(timestamp);
  return `${processedDate.getDate()}/${
    processedDate.getMonth() + 1
  }/${processedDate.getFullYear()}`;
};

export const nametoProfile = (name) => {
  return name.toLowerCase().replace(" ", "");
};

const LogList = () => {
  const route = useSelector((state) => state.route.route);
  // const routeArray = dataSorter(route, "descending")
  const routeArray = route.map((ele) => {
    return (
      <Link to={`/horb/logs/list/${ele.id}`}>
        <LogItem
          name={ele.name}
          profile={nametoProfile(ele.name)}
          route={ele.route}
          duration={ele.duration}
          time={timestampTo24(ele.time)}
          date={timestampToDate(ele.time)}
        />
      </Link>
    );
  });

  return (
    <div>
      <Section>
        <SectionHdr type="page">Logs</SectionHdr>
      </Section>
      <Switch>
        <Route path="/horb/logs/list" exact>
          <ul className={styles.list}>{routeArray}</ul>
        </Route>
        <Route path="/horb/logs/list/:logID">
          <LogDetail />
        </Route>
      </Switch>
    </div>
  );
};

export default LogList;
